const walkdir = require('walkdir');
const assert = require('assert');
const marked = require('marked');
const joanna = require('joanna');
const tello = require('tello');
const path = require('path');
//const fs = require('fs');

const SRC_DIRS = ['src'];
const BLACKLIST_FILES = ['Gruntfile.coffee'];
const mozillaJavascriptBaseUrl = 'https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects';
const mozillaWebBaseUrl = 'https://developer.mozilla.org/en-US/docs/Web/API';
const atomBaseUrl = 'https://atom.io/docs/api/latest';

const isAcceptableFile = function(filePath) {
  var file, i, len;
  try {
    if (fs.statSync(filePath).isDirectory()) {
      return false;
    }
  } catch (error) {}
  for (i = 0, len = BLACKLIST_FILES.length; i < len; i++) {
    file = BLACKLIST_FILES[i];
    if (new RegExp(file + '$').test(filePath)) {
      return false;
    }
  }
  return filePath.match(/\._?js$/);
};

const isInAcceptableDir = function(inputPath, filePath) {
  var acceptableDirs, dir, i, len;
  if (path.join(inputPath, path.basename(filePath)) === filePath) {
    // is in the root of the input?
    return true;
  }
  // is under src, lib, or app?
  acceptableDirs = (function() {
    var i, len, results;
    results = [];
    for (i = 0, len = SRC_DIRS.length; i < len; i++) {
      dir = SRC_DIRS[i];
      results.push(path.join(inputPath, dir));
    }
    return results;
  })();
  for (i = 0, len = acceptableDirs.length; i < len; i++) {
    dir = acceptableDirs[i];
    if (filePath.indexOf(dir) === 0) {
      return true;
    }
  }
  return false;
};

export default class Renderer {

  constructor() {
    this.compiledTemplates = {};
    this.initReferenceMap();
  }

  renderModules(sourcePaths, outPath, options = {}) {
    var absoluteInput, digestedMetadata, filename, i, input, j, jsMetadata, len, len1, metadata, ref1, sourceJSFiles, stats;
    assert(sourcePaths, 'requires sourcePaths param');
    assert(outPath, 'requires outPath param');
    if (!fs.existsSync(outPath)) {
      fs.mkdirSync(outPath);
    }
    sourceJSFiles = [];
    for (i = 0, len = sourcePaths.length; i < len; i++) {
      input = sourcePaths[i];
      stats = fs.lstatSync(input);
      absoluteInput = path.resolve(process.cwd(), input);
      if (stats.isDirectory()) {
        ref1 = walkdir.sync(input);
        for (j = 0, len1 = ref1.length; j < len1; j++) {
          filename = ref1[j];
          if (isAcceptableFile(filename) && isInAcceptableDir(absoluteInput, filename)) {
            sourceJSFiles.push(filename);
          }
        }
      } else {
        if (isAcceptableFile(input)) {
          sourceJSFiles.push(input);
        }
      }
    }
    console.log('jesse');
    console.log(sourceJSFiles);
    jsMetadata = joanna(sourceJSFiles);
    metadata = {
      repository: 'someurl', // packageJson.repository.url
      version: 'some version', // packageJson.version
      files: jsMetadata.files
    };
    console.log(JSON.stringify(jsMetadata, null, 2));
    //metadata = donna.generateMetadata(sourcePaths)
    digestedMetadata = tello.digest([metadata]);
    console.log(JSON.stringify(digestedMetadata, null, 2));
    this.metadata = digestedMetadata;
    this.saveClassList(outPath, options);
    return this.saveClasses(outPath, options);
  }

  saveClasses(outPath, options) {
    var clazz, name, ref1, renderedClazz, results;
    ref1 = this.metadata.classes;
    results = [];
    for (name in ref1) {
      clazz = ref1[name];
      renderedClazz = this.renderClass(clazz, options);
      results.push(fs.writeFileSync(path.join(outPath, `${name}.md`), renderedClazz));
    }
    return results;
  }

  saveClassList(outPath) {
    var classes, clazz, name, ref1, renderedClassList;
    classes = [];
    ref1 = this.metadata.classes;
    for (name in ref1) {
      clazz = ref1[name];
      classes.push(name);
    }
    classes.sort();
    renderedClassList = this.renderTemplate('class-list', {
      classes: classes
    });
    return fs.writeFileSync(path.join(outPath, 'class-list.html'), renderedClassList);
  }

  renderClass(clazz, options) {
    var content;
    clazz.descriptionSection = this.renderDescriptionSection(clazz);
    clazz.examplesSection = this.renderExamplesSection(clazz, options);
    clazz.sections = this.renderSections(clazz, options);
    clazz.layout = options.layout || 'default';
    clazz.classesPath = options.classesPath || '';
    content = this.renderTemplate('class', clazz);
    return content.replace(/\n\n/, '\n');
  }

  renderDescriptionSection(clazz) {
    if (clazz.description) {
      return this.renderTemplate('description-section', clazz, {
        resolve: ['description'],
        markdown: ['description']
      });
    }
  }

  renderExamplesSection(object, options) {
    var example, examples;
    if (!object.examples) {
      return '';
    }
    if (options != null ? options.verbose : void 0) {
      console.log("  Examples");
    }
    examples = ((function() {
      var i, len, ref1, results;
      ref1 = object.examples;
      results = [];
      for (i = 0, len = ref1.length; i < len; i++) {
        example = ref1[i];
        results.push(this.renderExample(example));
      }
      return results;
    }).call(this)).join('\n');
    return this.renderTemplate('examples', {
      examples: examples
    });
  }

  renderExample(example) {
    example.description = this.renderMarkdown(example.description);
    example.raw = this.renderReferences(example.raw);
    example.raw = this.renderMarkdown(example.raw);
    return this.renderTemplate('example', example);
  }

  renderSections(clazz, options) {
    var section, sections;
    sections = clazz.sections;
    sections.unshift({
      name: null
    });
    return ((function() {
      var i, len, results;
      results = [];
      for (i = 0, len = sections.length; i < len; i++) {
        section = sections[i];
        results.push(this.renderSection(clazz, section, options));
      }
      return results;
    }).call(this)).join('\n');
  }

  renderSection(clazz, section, options) {
    if (options != null ? options.verbose : void 0) {
      console.log(`  Section: ${section.name}`);
    }
    section.classProperties = this.renderProperties(clazz.classProperties, section.name, 'static', options);
    section.properties = this.renderProperties(clazz.instanceProperties, section.name, 'instance', options);
    section.classMethods = this.renderMethods(clazz.classMethods, section.name, 'static', options);
    section.methods = this.renderMethods(clazz.instanceMethods, section.name, 'instance', options);
    section.description = this.renderReferences(section.description);
    section.description = this.renderMarkdown(section.description);
    if (section.classProperties.length > 0 || section.properties.length > 0 || section.classMethods.length > 0 || section.methods.length > 0 || section.description.length > 0) {
      return this.renderTemplate('section', section);
    }
  }

  renderProperties(properties, sectionName, type, options) {
    var props;
    props = _.filter(properties, function(prop) {
      return prop.sectionName === sectionName;
    });
    props = _.map(props, (prop) => {
      return this.renderProperty(prop, type, options);
    });
    return props.join('\n');
  }

  renderProperty(property, type, options) {
    if (options != null ? options.verbose : void 0) {
      console.log(`    Property: ${property.name}`);
    }
    property.id = `${type}-${property.name}`;
    property.type = type;
    property.signature = `${this.renderSignifier(type)}${property.name}`;
    property.examples = this.renderExamplesSection(property, options);
    return this.renderTemplate('property', property, {
      resolve: ['description'],
      markdown: ['description']
    });
  }

  renderMethods(methods, sectionName, type, options) {
    methods = _.filter(methods, function(method) {
      return method.sectionName === sectionName;
    });
    methods = _.map(methods, (method) => {
      return this.renderMethod(method, type, options);
    });
    return methods.join('\n');
  }

  renderMethod(method, type, options) {
    if (options != null ? options.verbose : void 0) {
      console.log(`    Method: ${method.name}`);
    }
    method.id = `${type}-${method.name}`;
    method.type = type;
    method.signature = `${this.renderSignifier(type)}${this.renderSignature(method)}`;
    method.examples = this.renderExamplesSection(method, options);
    method.parameterBlock = method.arguments ? this.renderParameterBlock(method) : '';
    method.returnValueBlock = method.returnValues ? this.renderReturnValueBlock(method) : '';
    return this.renderTemplate('method', method, {
      resolve: ['description'],
      markdown: ['description']
    });
  }

  renderSignature(method) {
    var parameters;
    parameters = method.arguments ? this.renderParameters(method) : '';
    return `${method.name}(${parameters})`;
  }

  renderSignifier(type) {
    if (type === 'static') {
      return '.';
    } else {
      return '::';
    }
  }

  renderParameterBlock(method) {
    var parameter, rows;
    rows = (function() {
      var i, len, ref1, results;
      ref1 = method.arguments;
      results = [];
      for (i = 0, len = ref1.length; i < len; i++) {
        parameter = ref1[i];
        results.push(this.renderParameterRow(parameter));
      }
      return results;
    }).call(this);
    return this.renderTemplate('parameter-block-table', {
      rows: rows.join('\n')
    });
  }

  renderParameterRow(parameter) {
    parameter.description = this.renderReferences(parameter.description);
    parameter.description = this.renderMarkdown(parameter.description, {
      noParagraph: true
    });
    parameter.children = parameter.children ? this.renderParameterChildren(parameter.children) : '';
    return this.renderTemplate('parameter-block-row', parameter);
  }

  renderParameterChildren(children) {
    var child;
    children = ((function() {
      var i, len, results;
      results = [];
      for (i = 0, len = children.length; i < len; i++) {
        child = children[i];
        results.push(this.renderParameterChild(child));
      }
      return results;
    }).call(this)).join('\n');
    return this.renderTemplate('parameter-children', {
      children: children
    });
  }

  renderParameterChild(child) {
    return this.renderTemplate('parameter-child', child, {
      markdown: ['description'],
      resolve: ['description'],
      noParagraph: ['description']
    });
  }

  renderReturnValueBlock(method) {
    var returnValue, rows;
    rows = (function() {
      var i, len, ref1, results;
      ref1 = method.returnValues;
      results = [];
      for (i = 0, len = ref1.length; i < len; i++) {
        returnValue = ref1[i];
        results.push(this.renderReturnValueRow(returnValue));
      }
      return results;
    }).call(this);
    return this.renderTemplate('return-value-block-table', {
      rows: rows.join('\n')
    });
  }

  renderReturnValueRow(returnValue) {
    returnValue.description = this.renderReferences(returnValue.description);
    returnValue.description = this.renderMarkdown(returnValue.description, {
      noParagraph: true
    });
    return this.renderTemplate('return-value-block-row', returnValue);
  }

  renderParameters(method) {
    var argument, formatArgument, names;
    formatArgument = function(argument) {
      var opt;
      opt = argument.isOptional ? '<sup>?</sup>' : '';
      return `${argument.name}${opt}`;
    };
    names = (function() {
      var i, len, ref1, results;
      ref1 = method.arguments;
      results = [];
      for (i = 0, len = ref1.length; i < len; i++) {
        argument = ref1[i];
        results.push(formatArgument(argument));
      }
      return results;
    })();
    return names.join(', ');
  }

  renderReferences(text) {
    return text != null ? text.replace(/`?\{\S*\}`?/g, (match) => {
      if (match.match(/^`.*`$/)) {
        return match;
      }
      return this.renderReference(match);
    }) : void 0;
  }

  renderReference(ref) {
    var result;
    result = this.resolveReference(ref);
    if (typeof result === 'string') {
      return result;
    } else {
      return this.renderTemplate('reference', result);
    }
  }

  renderMarkdown(content, options = {}) {
    var output;
    if (!content) {
      return '';
    }
    output = marked(content, {
      gfm: true,
      smartypants: true,
      highlight: function(code, lang) {
        if (lang) {
          return highlightjs.highlight(lang, code).value;
        } else {
          return code;
        }
      }
    });
    if (options.noParagraph) {
      output = output.replace(/<\/?p>/g, '');
    }
    return output;
  }

  renderTemplate(templateName, locals, options = {}) {
    /*
    var content, field, i, j, len, len1, ref1, ref2, template, templatePath, templateSource;
    if (!(template = this.compiledTemplates[templateName])) {
      templatePath = path.join(path.dirname(__dirname), 'lib', 'templates', templateName + '.haml');
      templateSource = fs.readFileSync(templatePath).toString();
      template = hamlc.compile(templateSource, {
        escapeAttributes: false
      });
      this.compiledTemplates[templateName] = template;
    }
    if (options.resolve) {
      ref1 = options.resolve;
      for (i = 0, len = ref1.length; i < len; i++) {
        field = ref1[i];
        locals[field] = this.renderReferences(locals[field]);
      }
    }
    if (options.markdown) {
      ref2 = options.markdown;
      for (j = 0, len1 = ref2.length; j < len1; j++) {
        field = ref2[j];
        locals[field] = this.renderMarkdown(locals[field], {
          noParagraph: options.noParagraph
        });
      }
    }
    content = template(locals);
    return content.replace(/\n\n/, '\n');
    */
  }

  initReferenceMap() {
    var atom, i, item, j, k, len, len1, len2, mozillaJavascript, mozillaWeb, results;
    this.referenceMap = {};
    mozillaJavascript = ['Object', 'String', 'Array', 'Function', 'Boolean', 'Symbol', 'Error', 'Number', 'Date', 'RegExp', 'Infinity', 'NaN'];
    for (i = 0, len = mozillaJavascript.length; i < len; i++) {
      item = mozillaJavascript[i];
      this.addReference(item, `${mozillaJavascriptBaseUrl}/${item}`);
    }
    mozillaWeb = ['HTMLDocument'];
    for (j = 0, len1 = mozillaWeb.length; j < len1; j++) {
      item = mozillaWeb[j];
      this.addReference(item, `${mozillaWebBaseUrl}/${item}`);
    }
    atom = ['Color', 'CommandRegistry', 'CompositeDisposable', 'Config', 'Decoration', 'Disposable', 'Emitter', 'Marker', 'Notification', 'NotificationManager', 'Point', 'Range', 'TextEditor', 'TooltipManager', 'ViewRegistry', 'Workspace', 'BufferedNodeProcess', 'BufferedProcess', 'Clipboard', 'ContextMenuManager', 'Cursor', 'DeserializerManager', 'Directory', 'File', 'GitRepository', 'Grammar', 'GrammarRegistry', 'KeymapManager', 'MenuManager', 'PackageManager', 'Pane', 'Panel', 'Project', 'ScropeDescriptor', 'Selection', 'StyleManager', 'Task', 'TextBuffer', 'ThemeManager'];
    results = [];
    for (k = 0, len2 = atom.length; k < len2; k++) {
      item = atom[k];
      results.push(this.addReference(item, `${atomBaseUrl}/${item}`));
    }
    return results;
  }

  addReference(name, url) {
    return this.referenceMap[name] = url;
  }

  resolveReference(text) {
    var item, itemText, klass, type;
    itemText = text.replace(/\{(.*)\}/, '$1');
    if (itemText.indexOf('.') !== -1) {
      type = 'static';
      [klass, item] = itemText.split('.');
    } else if (itemText.indexOf('::') !== -1) {
      type = 'instance';
      [klass, item] = itemText.split('::');
    }
    switch (type) {
      case 'static':
        return this.resolveStaticReference(klass, item, itemText);
      case 'instance':
        return this.resolveInstanceReference(klass, item, itemText);
      default:
        return this.resolveClassReference(itemText, text);
    }
  }

  resolveStaticReference(klass, item, text) {
    switch (false) {
      case klass !== '':
        return {
          name: text,
          url: `#static-${item}`
        };
      case !this.metadata.classes[klass]:
        return {
          name: text,
          url: `${klass}.html#static-${item}`
        };
      case !this.referenceMap[klass]:
        return {
          name: text,
          url: `${mozillaJavascriptBaseUrl}/${klass}/${item}`
        };
    }
  }

  resolveInstanceReference(klass, item, text) {
    switch (false) {
      case klass !== '':
        return {
          name: text,
          url: `#instance-${item}`
        };
      case !this.metadata.classes[klass]:
        return {
          name: text,
          url: `${klass}.html#instance-${item}`
        };
      case !this.referenceMap[klass]:
        return {
          name: text,
          url: `${mozillaJavascriptBaseUrl}/${klass}/${item}`
        };
    }
  }

  resolveClassReference(klass, text) {
    var ref1, ref2;
    switch (false) {
      case !((ref1 = this.metadata) != null ? (ref2 = ref1.classes) != null ? ref2[klass] : void 0 : void 0):
        return {
          name: klass,
          url: klass + '.html'
        };
      case !this.referenceMap[klass]:
        return {
          name: klass,
          url: this.referenceMap[klass]
        };
      default:
        return text;
    }
  }

};



